  margin: 0 auto;
}

.row {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.col-6 {
  width: 50%;
}

@media (max-width: 1087px) {
  .container {
    width: 100%;
    padding: 0 30px;
  }

