.navbar {
  background-color: #000 !important;
  height: 70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.2rem;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 20;
}

.nav-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 80px;
  max-width: 1500px;
}

.nav-logo {
  color: #fff;
  -webkit-align-items: center;
          align-items: center;
  margin-left: 50px;
  cursor: pointer;
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 600;
}

.nav-menu {
  display: -webkit-flex;
  display: flex;
  list-style: none;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-right: 2rem;
}

.nav-links {
  color: #fff;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
}

.nav-item {
  line-height: 40px;
  margin-right: 1rem;
}

.nav-item:after {
  content: '';
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.7s ease;
}

.nav-item:hover:after {
  width: 100%;
  background: #fff;
}

.nav-item .active {
  color: #fff;
}

.nav-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .nav-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    position: absolute;
    top: 70px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #000 !important;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #fff;
  }
} ;

.hero-container {
  background: url('https://www.agllaw.com.vn/wp-content/uploads/2015/02/ethree-website-cover.jpg')
    center center/cover;
  height: 100vh;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  object-fit: contain;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
}

.hero-container > h1 {
  margin-top: 10px;
  color: #fff;
  font-size: 100px;
}

.hero-container .typed-anim {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
  background-color: #000;
  padding: 4px 10px;
}

.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
  margin: 6px;
}

@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 70px;
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 50px;
    position: absolute;
    top: 30%;
  }
  .hero-container > p {
    font-size: 30px;
    position: absolute;
    top: 230px;
  }

  .hero-btns {
    position: absolute;
    top: 60%;
  }
  video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: static;
    z-index: -1;
  }

  .hero-container {
    height: 50vh;
    object-fit: contain;
  }
}

@media screen and (max-width: 449px) {
  .hero-container > h1 {
    font-size: 30px;
    position: absolute;
    top: 30%;
  }
  .hero-container > p {
    font-size: 20px;
    position: absolute;
    top: 35%;
  }

  .hero-btns {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    top: 60%;
  }

  .hero-btns .btn {
    width: 100%;
    font-size: 0.6rem;
    margin-top: -10%;
  }

  .hero-container {
    height: 50vh;
    object-fit: contain;
  }

  .hero-container .typed-anim {
    margin-top: 40px;
    font-size: 20px;
  }
}

:root {
  --primay: #fff;
}

.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 500;
}

.btn--primary {
  background-color: #c770f0;
  color: #000;
  border: 1px solid #c770f0;
}

.btn--outline {
  background-color: transparent;
  color: #fff;
  padding: 8px 20px;
  border: 1px solid #fff;
  border: 1px solid var(--primay);
  transition: all 0.3s ease;
}

.btn--outline-b {
  background-color: transparent;
  color: #000;
  padding: 8px 20px;
  border: 1px solid #000;
  transition: all 0.3s ease;
}

.btn--medium {
  padding: 4px 10px;
  font-size: 14px;
}

.btn--large {
  padding: 12px 26px;
  font-size: 20px;
}

.btn--medium:hover,
.btn--large:hover {
  background: #fff;
  color: #242424;
  transition: all 0.3s ease;
}

.cards {
  margin-bottom: 7rem;
}

.cards > h1 {
  text-align: center;
  text-transform: uppercase;
  font-size: 41px;
  margin-bottom: 3rem;
}

.cards__row {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 3rem;
  -webkit-justify-content: center;
          justify-content: center;
}

.card {
  margin: 10px;
  box-shadow: 0 15px 35px rgba(50, 50, 93, 0.1), 0 5px 15px rgba(0, 0, 0, 0.07) !important;
  transition: -webkit-transform 100ms ease-in;
  transition: transform 100ms ease-in;
  transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
  cursor: pointer;
  height: 400px;
  width: 400px;
  text-decoration: none;
  color: #000;
  overflow: hidden;
}

.card:hover {
  -webkit-transform: scale(1.07);
          transform: scale(1.07);
}

.card > img {
  object-fit: fill;
  width: 100%;
  max-height: 250px;
}

.card__info {
  margin-top: -9px;
  padding: 10px;
  border-radius: 10px;
  padding-top: 20px;
  border: 1;
}

.card__info > h2 {
